.rows__content {
  margin-top: 10rem;
  border-bottom: 6px solid #313030;
}

.rows__content .row {
  margin: 0;
  padding: 0;
  border-top: 6px solid #313030;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.2rem;
}

.rows__content .row .row__desc h1 {
  font-size: 3rem;
  margin-bottom: 13px;
}

.rows__content .row .row__desc p {
  font-size: 1.5rem;
}

.row .row__image {
  height: 320px;
}

.row .row__image img {
  height: 100%;
}

.FAQS {
  margin: 4rem 0;
}
.FAQS h1 {
  text-align: center;
  font-size: 3rem;
  margin-bottom: 3rem;
}

.FAQS .faq {
  width: 70%;
  margin: 10px auto;
  background: #303030;
  max-width: 768px;
  transition: all 0.2s ease-in-out;
}

.FAQS .accordion__header {
  font-size: 1.8rem;
  border-bottom: 1px solid black;
  padding: 15px 0px;
  padding-left: 30px;
  position: relative;
  cursor: pointer;
}

.FAQS .accordion__header::after {
  content: "+";
  position: absolute;
  line-height: 2rem;
  height: 2rem;
  right: 30px;
  font-size: 3.5rem;
  transition: 0.2s ease;
}

.FAQS .accordion__body {
  opacity: 0;
  overflow-y: hidden;
  max-height: 0;
  transition: 0.2s ease-in-outs;
}

.FAQS .faq.open .accordion__header::after {
  transform: rotate(45deg);
}

.FAQS .faq.open .accordion__body {
  max-height: 500px;
  opacity: 1;
  padding: 20px 30px;
  font-size: 1.6rem;
}

.footer {
  width: 100%;
  margin-top: 4rem;
  padding: 3rem 2rem 0.5rem 2rem;
  padding-left: 5rem;
  color: #757575;
  border-top: 6px solid #313030;
}

.footer .footer__header {
  color: #757575;
  margin-bottom: 20px;
}

.footer a {
  text-decoration: none;
  color: #757575;
}

.footer a:hover {
  text-decoration: underline;
}

.footer .footer__middle {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(12rem, 1fr));
}

.footer .footer__middle a {
  color: #757575;
  padding: 5px 0px;
  font-size: 0.9rem;
}

.footer__bottom {
  text-align: center;
  margin-top: 30px;
  letter-spacing: 0.2rem;
}

.footer__bottom span {
  color: #b3b2b2;
}

.signin__page {
  background-image: url("https://assets.nflxext.com/ffe/siteui/vlv3/9bab8662-5230-490a-9da7-d5006d0fc254/5ef8e0d9-ea4c-4392-a6f8-262274f94b83/IN-vlv-thali-small.jpg");
  height: 100vh;
  background-size: cover;
  background-position: center;
  position: relative;
  z-index: 1;
}

.signin__page::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: -1;
}

.header img {
  z-index: 2;
  height: 100px;
  width: 180px;
  object-fit: contain;
  margin-left: 20px;
}

.signin__box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.75);
  width: 400px;
  padding: 2rem 3rem;
}

.signin__box h2 {
  font-size: 2rem;
  letter-spacing: 0.7px;
  margin-bottom: 10px;
}

.signin__page .input__box {
  width: 100%;
}

.input__box .input {
  width: 100%;
  padding: 16px 13px;
  margin: 8px 0;
  border-radius: 3px;
  border: none;
  outline: none;
  background: #333;
  color: white;
  font-size: 1rem;
}

.input__box .submit a {
  color: white;
  text-decoration: none;
  padding: 15px 20px;
}

.input__box .submit {
  background: #e50914;
  width: 100%;
  margin-top: 20px;
  padding: 15px;
  border-radius: 5px;
  border: none;
  outline: none;
  color: #fff;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
}

::-webkit-input-placeholder {
  color: #a8a8a8;
}

:-ms-input-placeholder {
  color: #a8a8a8;
}

::-ms-input-placeholder {
  color: #a8a8a8;
}

::placeholder {
  color: #a8a8a8;
}

.help {
  display: flex;
  /* border: 1px solid white; */
  justify-content: space-between;
  font-size: 0.9rem;
  margin: 8px 0;
}

.help a {
  color: #a8a8a8;
  text-decoration: none;
}

.row {
  margin-left: 10px;
}

.row h2 {
  letter-spacing: 1px;
}

.row-posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
}

.row-posters::-webkit-scrollbar {
  display: none;
}
.row-image {
  max-height: 100px;
  width: 100%;
  object-fit: contain;
  cursor: pointer;
  transition: transform 450ms;
  margin-right: 10px;
}

.row-image:hover {
  transform: scale(1.08);
}

.row-image-large {
  max-height: 200px;
}

.row-image-large:hover {
  transform: scale(1.09);
}

.banner {
  color: #ffff;
  height: 450px;
  object-fit: contain;
  position: relative;
}

.banner-content {
  position: absolute;
  top: 40%;
  /* border: 1px solid white; */
  height: 190px;
  padding: 30px;
}

.banner-title {
  letter-spacing: 1px;
  font-size: 2.5rem;
  font-weight: bold;
  padding-bottom: 0.4rem;
}

.banner-description {
  max-width: 30rem;
  line-height: 1.3;
  font-size: 0.94rem;
  padding-top: 1rem;
  letter-spacing: 0.7px;
}

.banner-button {
  border: none;
  outline: none;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px 2rem;
  margin-right: 10px;
  font-weight: 700;
  cursor: pointer;
  border-radius: 0.2rem;
  transition: all 0.4s ease-in;
}

.banner-button:hover {
  background: #fff;
  color: #000;
}

.banner-fade {
  position: absolute;
  left: 0;
  height: 5rem;
  width: 100%;
  bottom: 0;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.6),
    #111111
  );
}

@media only screen and (max-width: 600px) {
  .banner-title {
    font-size: 2rem;
  }
}

.nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 50px;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.1rem;
  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav-black {
  background-color: #000;
}

.nav .logo {
  object-fit: contain;
  width: 90px;
}

